.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

header {
  background: linear-gradient(90deg, black 28%, #69a0bc);
  width: 100%;
  height: 300px;
  z-index: 1;
  overflow: hidden;
  margin-bottom: 50px;
}

.carousel-item img {
  height: 200px;
}

body { 
  margin-bottom: 50px;
}
.main-body {  
}

.card-box {

}

.card-wrapper { 
  height: auto;
  margin-right: 10px;
  text-align: center; 
  border-radius: 10px 10px 0 0;
  border: 1px solid black;
}

.card-wrapper img {
  height: 250px;
  width: 100%;
  border-radius: 10px 10px 0 0;
}

.card-wrapper p {
  padding: 5px 5px 10px 10px;
}


.card-wrapper button {
  margin: 5px 5px 10px 5px;
  border-radius: 5px;
  color: white;
  background-color: blue;
  border: none;
  padding: 5px 10px 5px 10px;
}

.input-search {
  margin-top: 10px;
  margin-left: 100px;
  height: 30px;
  font-size: 14px;
}

.activeNav {
  color: #c9c9c7;
}

.navbar {
  padding: 30px 0;
}

.nav-item a {
  font-size: 20px;
}

a {
  text-decoration: none;
  color: black;
}

.navbar-brand {
  color: white;
  font-size: 25px;
  font-weight: 700;
}

@media screen and (max-width: 768px){
  .input-search {
    margin-left: 0px; 
  }
}

footer {
  width: 100%;
  height: auto;
}
